.goback {
  column-gap: 5px;
  width: auto;
}
.pageName {
  font-size: 20px;
  font-weight: 600;
  color: #090909;
}
.ApprovedPropertyStatus {
  display: flex;
  padding: 2px 7px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-left: 10px;
  border-radius: 4px;
  background: #d9f5e4;
}
.RejectedPropertyStatus {
  display: flex;
  text-align: center;
  text-transform: capitalize;
  padding: 2px 7px;
  margin-left: 10px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 4px;
  background: #ffe3e3;
}
.PendingPropertyStatus {
  display: flex;
  text-align: center;
  text-transform: capitalize;
  padding: 2px 7px;
  margin-left: 10px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 4px;
  background: #ffeee4;
}
.ApprovedProperty {
  color: #1f9a50;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  text-align: center;
  text-transform: capitalize;
}
.RejectedProperty {
  color: #bf3939;
  font-size: 13px;
  font-style: normal;
  text-transform: capitalize;
  text-align: center;
  font-weight: 500;
}
.PendingProperty {
  color: #d45b0b;
  font-size: 13px;
  font-style: normal;
  text-transform: capitalize;
  text-align: center;
  font-weight: 500;
}
